.github-main {
    /* border: 1px solid red; */
    background-color: #212425;
    padding: 2px 0px 30px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* gap: 10px; */
}

.github-heading {
    font-size: 40px;
}

.github-calender {
    /* border: 1px solid rgb(46, 255, 4); */
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    padding: 2%;
    width: 75%;
    border-radius: 8px;
    margin-bottom: 2%;
}

.github-stats {
    /* border: 1px solid rgb(46, 255, 4); */
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 75%;
    height: 200px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    border-radius: 8px;
}

#github-streak {
    width: 50%;
}

#github-language {
    width: 40%;
}

#github-streak-status {
    width: 50%;
}

/* meidum screens */

@media all and (min-width: 768px) and (max-width: 1024px) {
    .github-calender {
        width: 75%;
    }
    .github-stats {
        /* border: 1px solid rgb(255, 5, 5); */
        display: flex;
        flex-direction: column;
        gap: 20px;
        justify-content: space-between;
        align-items: center;
        width: 75%;
        height: auto;
        box-shadow: none
    }
    #github-streak {
        display: flex;
        justify-content: space-around;
        /* border: 1px solid red; */
        width: 100%;
        margin: auto;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
        border-radius: 8px;
    }
    #github-language {
        /* border: 1px solid red; */
        display: flex;
        justify-content: space-around;
        width: 100%;
        margin: auto;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
        border-radius: 8px;
    }
    #github-streak-status {
        /* border: 1px solid red; */
        display: flex;
        justify-content: space-around;
        width: 100%;
        margin: auto;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
        border-radius: 8px;
    }
}

